        function findMaxDailySale(arr) {
            var max = arr[0].y;
            var indexOfMax = 0;
            for (var i = 0; i < arr.length; i++) {
                if (arr[i].y >= max) {
                    max = arr[i].y;
                    indexOfMax = i;
                }
            }
            arr[indexOfMax].color = '#F7BE00';
            return max;
        }

